@media (min-width: 768px) {
  .col-primary {
    width: 100%;
    float: left;
    margin-right: -310px;
    padding-right: 310px; }

  .col-secondary {
    width: $sidebar-width;
    float: left;
    margin-left: 10px;
    //.x-column-fix
 }    //width: $sidebar-width
  .box-special {
    .col-secondary {
      margin-left: 0; } } }
@media (max-width: 768px) {
  .box-mod .cell:last-of-type:after {
    content: none; }
  .card-1x1 .cont h1 a {
    font-size: 4rem; }
  .hdr-body .envivo span.titu {
    display: none; }

  .x-gallery {
    &:-webkit-full-screen {
      background: #292929; }
    .x-pager-text {
      text-transform: lowercase; }
    .x-caption {
      p {
        margin-right: 0; }
      .social {
        position: static;
        margin-top: 0;
        float: right; } }
    .x-n-foto {
      display: none; }
    .x-prev, .x-next {
      opacity: 1!important;
      top: 50%;
      width: auto;
      height: auto;
      span {
        opacity: .7!important;
        font-size: 3.5rem;
        &.icon-prev {
          left: 1rem; }
        &.icon-next {
          right: 1rem; } } }
    .ep_bloquer {
      .ad-close {
        bottom: 16px; } } } }

@media (max-width: 768px) and (orientation: portrait) {
  .x-gallery {}
  &:-moz-full-screen {
    .x-wrapper {
      top: 25%; } } }

@media (min-width: 480px) and (max-width: 768px) {
