@media (max-width: 992px) {
  .issues {
    display: block; }
  .hdr-top {
    display: none; }
  .hdr-body {
    &>.inner {
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.34);
      border-top: 2px solid #0060ab; }

    position: relative;
    //border-top: 2px solid $color-base-bg
    //border-bottom: 1px solid #bababa
    .content-side {
      .col {}
      .col.col-live, .col.col-resume {
        padding: 0 6px;
 }        //border-left: 1px solid #dcdcdc
      //.col:nth-child(3)
 }      //border-left: 1px solid #dcdcdc;
    .logo {
      margin-left: 10px;
      margin-top: 0;
      img {
        height: 36px;
        width: inherit;
 } }        //margin-top: 1px
    .envivo {
      margin: 0;
      float: right;
      //span.titu
 }      //display: none
    .resume-link {
      margin: 0;
      float: right;
      span.titu {
        display: none; } }
    //.social.inverse
    //display: none
    .nav-right {
      height: 38px;
      li.opt-search {
        display: none; } } }
  .hdr-foot {
    display: none; }

  .footer-brands {
    position: relative;
    padding-top: 37px;
    padding-bottom: 8px;
    padding-left: 25px;
    padding-right: 25px;
    .grid {
      position: static; }

    .copy {
      left: 50%;
      margin-left: -23px;
      width: 46px;
      .copyright {
        display: none; } }
    ul {}
    li {
      display: inline-block;
      float: none; } }
  #x-fixed-menutop.fixed-on {
    height: 40px; }
  .wrap-especial {
    .especial-btn {
      display: inline-block; }
    .pull-right {
      display: none;
      float: inherit;
      li {
