#widget-flash {
	width: 100%;
	height: 290px;
	background: #fff url(../img/flash-electoral-2016/bg-flash-electoral-2016.jpg) 0 0 repeat-x;
	padding: 10px 15px;
	position: relative;
	margin: 0px auto 10px auto;
	border-top: 2px solid #0060ab;
	.loader_widget {
		position: absolute;
		top: 35%; }
	h1.wf-title {
		color: #972828;
		font-size: 24px;
		font-weight: bold;
		font-family: 'Myriad Pro';
		text-transform: uppercase;
		display: inline-block;
		margin: 0; }
	nav.wf-menu {
		display: inline-block;
		float: right;
		ul {
			list-style: none;
			text-transform: uppercase;
			margin: 0;
			li {
				display: inline-block;
				font-size: 16px;
				font-weight: 500;
				padding: 0 5px;
				a {
					color: #544949;
					font-family: 'Myriad Pro';
					font-weight: 600; }
				a:hover {
					color: #972828; } }
			li.this {
				a {
					color: #972828; } }
			li.special {
				margin-left: 20px;
				a {
					color: #544949; } } } }
	.results {
		width: 100%;
		overflow: hidden;
		.resultados-encuestadora {
			width: 940px;
			margin: 0 auto;
			text-align: center; }
		.candidato {
			// width: 16.666%
			display: inline-block;
			vertical-align: middle;
			margin-right: 32px;
			.img-c {
				width: 99px;
				height: 99px;
				border-radius: 50%;
				margin: 0 auto;
				background: #fff url(../img/flash-electoral-2016/sprite_candidatos.png);
				background-repeat: no-repeat;
				background-position: 0px 133px;
				margin-bottom: 5px; }
			// fotos
			.img-c-fujimori {
				background-position: 0 -168px; }
			.img-c-kuczynski {
				background-position: -802px -168px; }
			.img-c-barnechea {
				background-position: -348px -168px; }
			.img-c-mendoza {
				background-position: -1045px -168px; }
			.img-c-garcia {
				background-position: -114px -168px; }
			.img-c-toledo {
				background-position: -235px -168px; }
			.img-c-olivera {
				background-position: -584px -168px; }
			.img-c-araoz {
				background-position: -465px -168px; }
			.img-c-hilario {
				background-position: -926px -168px; }
			.img-c-santos {
				background-position: -698px -168px; }
			.datos-c {
				background-color: transparent;
				font-family: 'Myriad Pro';
				font-weight: 600;
				text-transform: uppercase;
				width: 115px;
				text-align: center;
				margin: 0 auto;
				padding: 2px 0;
				.nombre-c {
					color: #373737;
					font-size: 14px; }
				.partido-c {
					color: #484848;
					font-size: 12.5px; } }
			.porcentaje-c {
				width: 115px;
				margin: 0 auto;
				text-align: center;
				font-size: 37px;
				line-height: 37px;
				font-family: 'Myriad Pro';
				font-weight: 600;
				color: #fff;
				.barra-total {
					width: 115px;
					height: 7px;
					background-color: #e6edf1;
					position: relative;
					.barra-c {
						position: absolute;
						background-color: #fff;
						width: 0%;
						height: 7px; } } } }
		// segunda vuelta
		.candidato.no-segunda {
			margin-top: 36px; }
		.candidato.segunda {
