	.mesas {
		color: #9b4d4d;
		font-size: 14px;
		position: absolute;
		bottom: 5px;
		right: 10px;
		font-family: 'Myriad Pro';
		p {
			margin: 0; } } }

@media (max-width: 650px) {
	#widget-flash {
		height: inherit !important;
		min-height: 290px;
		background: #fff url(../img/flash-electoral-2016/bgmovil-flash-electoral-2016.jpg) 0 0 repeat-y;
		padding: 5px;
		.wf-top {
			margin-top: 10px; }
		h1.wf-title {
			text-align: center;
			width: 100%;
			font-size: 32px !important;
			line-height: 32px; }
		nav.wf-menu {
			// float: left
			ul {
				margin-top: 15px;
				li {
					font-size: 1.5rem; }
				li.special {
					margin-left: 0; } } }
		.results {
			.candidato {
				margin-right: 0;
				margin-bottom: 15px;
				margin-right: 15px !important;
				width: 110px !important; }
			.resultados-encuestadora {
				width: 100%;
				padding-left: 8%; } }
		.mesas {
			position: relative;
			bottom: inherit;
			right: inherit;
			text-align: center; } } }
@media (max-width: 768px) {
	#widget-flash {
		height: 290px;
		h1.wf-title {
			font-size: 20px;
			text-align: center;
			display: block;
			margin: 3px 0; }
		nav.wf-menu {
			float: inherit;
			text-align: center;
			display: block; }
		.results {
			.candidato {
				margin-right: 2px;
				// width: 100px
				width: 111px;
				vertical-align: inherit;
				.img-c {
					transform: scale(0.75); }
				.porcentaje-c {
					width: 95px;
					font-size: 26px;
					// line-height: 32px
					.barra-total {
						width: 95px; } } }
			.candidato.segunda {
				margin-top: -20px;
				.img-c {
					margin-left: -18px; }
				.datos-c {
					width: 95px;
					.nombre-c {
						font-size: 13px; } } }
			.candidato.no-segunda {
				margin-top: 0; }
			.resultados-encuestadora {
				width: 100% !important; } } } }
@media (max-width: 667px) {
	#widget-flash {
		.results {
			.candidato {
				width: 100px;
				.img-c {
					transform: scale(0.7); } } } } }
