#header {
  background-color: #fff;
  //position: relative
 }  //z-index: 2
.hdr-top {
  border-bottom: 1px solid #e4e4e4;
  height: 21px;
  .fecha {
    float: left;
    text-transform: uppercase;
    font-size: 1.2rem;
    margin-top: 1px; }
  .nav-top {
    float: right;
    margin: 5px 0 0;
    ul {
      list-style: none;
      line-height: 1;
      margin: 0;
      li {
        text-transform: uppercase;
        float: left;
        font-size: 1.2rem;
        margin-left: 8px;
        margin-right: 8px;
        margin-bottom: 0;
        position: relative;
        &:after {
          position: absolute;
          top: 25%;
          right: -8px;
          height: 50%;
          width: 1px;
          content: ""; }
        &:last-child:after {
          content: none;
          padding-right: 0; }
        a {
          border-bottom: 3px solid transparent;
          color: #666;
          text-decoration: none;
          display: block;
          padding-bottom: 1px;
          &.active, &:hover {
            color: #0060ab;
            border-bottom-color: #0060ab; } } } } } }
.hdr-body {
  position: relative;
  //.social
  //position: absolute
  //right: 10px
  //bottom: 10px
  //li
  //  margin-right: 0
  //  margin-left: 10px
  //  &:first-child
  //    margin-left: 0
  .social {
    li {
      a {
        @include transition(background-color);
        &:hover {
          color: white; } } }
    li.fb {
      a:hover {
        background-color: $facebook-color; } }
    li.tw {
      a:hover {
        background-color: $twitter-color; } }
    li.yt {
      a:hover {
        background-color: $youtube-color; } }
    li.rss {
      a:hover {
        background-color: $rss-color; } } }














  .content-side {
    display: table;
    float: right;
    .col {
      height: 100%;
      vertical-align: middle;
      margin: 0;
      display: table-cell;
      &:last-child {
        padding: 0; } }
    .col.col-social {
      border-right: 1px solid #dcdcdc;
      width: 165px;
      padding: 0;
      text-align: center;
      padding-top: 4px; } }
  .logo {
    float: left;
    margin-top: 3px; }

  .envivo {
    background-color: $logo-base-color;
    border-radius: 4px;
    font-size: 1.4rem;
    padding: 5px 0 5px 0;
    margin: 0;
    color: #000;
    text-transform: uppercase;
    span.titu {
      margin-right: 10px;
      strong {
        max-width: 200px;
